import React from 'react';
import { Form, Button } from 'react-bootstrap';

/**
 * 
 * @param {Array} users - DB
 * @param {string} selectUserID - ID
 * @param {Function} setSelectUserID - ID
 * @param {Function} handleDeleteUser - 
 */
const UserList = ({ users, selectUserID, setSelectUserID, handleDeleteUser }) => {
    return (
        <div className="container mt-4">
            <Form.Group controlId="users">
                <Form.Label></Form.Label>
                <Form.Control
                    as="select"
                    value={selectUserID}
                    onChange={(e) => setSelectUserID(e.target.value)}
                >
                    <option value=""></option>
                    {users.map((user, index) => (
                        <option key={user.id || index} value={user.id}>
                            name: {user.name}, location: {user.location}
                        </option>
                    ))}
                </Form.Control>
            </Form.Group>
            <Button variant="danger" onClick={handleDeleteUser}>
                DB
            </Button>
        </div>
    );

};

export default UserList;
