import React from 'react';
import { Form, Button } from 'react-bootstrap';

/**
 * 
 * @param {string} name - 
 * @param {Function} setName - 
 * @param {string} location - 
 * @param {Function} setLocation - 
 * @param {Array} prefectures - 
 * @param {Function} handleAddUser - 
 * @param {Function} handleSelectChange - 
 */
const UserForm = ({ name, setName, location, setLocation, prefectures, handleAddUser, handleSelectChange }) => {
    return (
        <div className="container mt-4">
            <Form>
                <Form.Group controlId="formUserName">
                    <Form.Label></Form.Label>
                    <Form.Control
                        type="text"
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        placeholder=""
                    />
                </Form.Group>
                <Form.Group controlId="formUserLocation">
                    <Form.Label></Form.Label>
                    <Form.Control
                        as="select"
                        value={location}
                        onChange={handleSelectChange}
                    >
                        <option value=""></option>
                        {prefectures.map((pref) => (
                            <option key={pref.prefCode} value={pref.prefName}>
                                {pref.prefName}
                            </option>
                        ))}
                    </Form.Control>
                </Form.Group>
                <Button variant="primary" onClick={handleAddUser}>
                    or
                </Button>
            </Form>
        </div>
    );

};

export default UserForm;
